import { rest } from 'msw'
import APPLYING_CLUBS from './data/applyingClubs'
import CLUBS from './data/clubs'
import forms from './data/forms'

interface SearchRequestBody {}
interface SearchRequestParams {}
interface SearchResponseBody {
  clubs: {
    id: number
    name: string
    type: string
    category: string
  }[]
  numPages: number
}

interface FormRequestBody {}
interface FormRequestParams {}
interface FormResponseBody {
  question1: string
  question2: string
  question3: string
}

interface ApplyRequestBody {
  id: number
  answer1: string
  answer2: string
  answer3: string
}
interface ApplyRequestParams {}
interface ApplyResponseBody {}

interface ApplyingRequestBody {}
interface ApplyingRequestParams {}
type ApplyingResponseBody = ApplyingClubType[]

interface ApplyingClubType {
  clubId: number
  name: string
  type: string
  category: string
  location: string
}

export const ClubHandlers = [
  rest.get('/club', (req, res, ctx) => {
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({ id: 1, category: 'DEPARTMENT_CLUB', type: '순수과학', name: 'Treeflex', location: '학생회관' })
    )
  }),
  rest.get('/club/recruit', (req, res, ctx) => {
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({
        semester: 'WINTER2023',
        contents:
          '<p>KWEB 회원 평균 외모</p><p><img src="https://photo.newsen.com/news_photo/2021/03/17/202103171028406210_1.jpg"></p><p>요정도</p>',
        startAt: '2023-01-03',
        endAt: '2023-02-01',
        limit: 50
      })
    )
  }),
  rest.get<SearchRequestBody, SearchRequestParams, SearchResponseBody>('/search/clubs', (req, res, ctx) => {
    const { searchParams } = req.url
    for (const [key, value] of searchParams) searchParams.set(key, decodeURIComponent(value))
    let clubs = CLUBS
    if (searchParams.has('category')) {
      const category = searchParams.get('category')
      if (!['POLITICS', 'RELIGION', 'VOLUNTEER', 'SCIENCE', 'IT', 'ARTSANDSPORTS', 'STUDY'].includes(category)) {
        return res(
          ctx.status(400, '카테고리 값이 올바르지 않습니다'),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
      clubs = clubs.filter(club => club.category === category)
    }
    if (searchParams.has('type')) {
      const type = searchParams.get('type')
      if (!['DEPARTMENT_CLUB', 'CENTRAL_CLUB', 'UNION_CLUB'].includes(type)) {
        return res(
          ctx.status(400, '종류 값이 올바르지 않습니다'),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
      clubs = clubs.filter(club => club.type === type)
    }
    if (searchParams.has('name')) {
      const query = searchParams.get('name')
      clubs = clubs.filter(club => club.name.toLowerCase().includes(query))
    }
    let show = 20
    if (searchParams.has('show')) {
      show = parseInt(searchParams.get('show'))
      if (show < 0) {
        return res(
          ctx.status(400, '개수 값이 올바르지 않습니다'),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
    }
    const numPages = Math.ceil(clubs.length / show)
    let page = 1
    if (searchParams.has('page')) {
      page = parseInt(searchParams.get('page'))
      if (page < 0 || page > numPages) {
        return res(
          ctx.status(400, '페이지 값이 올바르지 않습니다'),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
    }
    if (clubs.length > show * page) clubs = clubs.slice(show * (page - 1), show * page)
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({ clubs, numPages })
    )
  }),
  rest.get<FormRequestBody, FormRequestParams, FormResponseBody>('/apply/form', (req, res, ctx) => {
    const accessToken = req.headers.get('Authorization')
    if (accessToken !== 'Bearer ACCESS_TOKEN') {
      return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    const { searchParams } = req.url
    if (!searchParams.has('clubId')) {
      return res(
        ctx.status(400, '쿼리가 올바르지 않습니다'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    const clubId = parseInt(decodeURIComponent(searchParams.get('clubId')))
    if (clubId <= 0) {
      return res(
        ctx.status(400, '동아리 아이디 값이 올바르지 않습니다'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json(forms[(clubId - 1) % forms.length])
    )
  }),
  rest.post<ApplyRequestBody, ApplyRequestParams, ApplyResponseBody>('/club/apply', async (req, res, ctx) => {
    const accessToken = req.headers.get('Authorization')
    if (accessToken !== 'Bearer ACCESS_TOKEN') {
      return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    const { clubId, answer1, answer2, answer3 } = await req.json()
    const { question1, question2, question3 } = forms[(clubId - 1) % forms.length]
    if ((!question1 && answer1) || (!question2 && answer2) || (!question3 && answer3)) {
      return res(
        ctx.status(400, '제출된 답안에 문제가 있습니다'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    return res(ctx.status(200), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
  }),
  rest.get<ApplyingRequestBody, ApplyingRequestParams, ApplyingResponseBody>('/apply/clubs', async (req, res, ctx) => {
    const { searchParams } = req.url
    for (const [key, value] of searchParams) searchParams.set(key, decodeURIComponent(value))
    let applyingClubs = APPLYING_CLUBS
    if (!searchParams.has('page')) {
      return res(
        ctx.status(400, '페이지 값이 올바르지 않습니다'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    const page = parseInt(searchParams.get('page'))
    if (!searchParams.has('count')) {
      return res(
        ctx.status(400, '개수 값이 올바르지 않습니다'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    const count = parseInt(searchParams.get('count'))
    if (applyingClubs.length > count * page) applyingClubs = applyingClubs.slice(count * (page - 1), count * page)
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json(applyingClubs)
    )
  })
]
