import { rest } from 'msw'

interface RegisterRequestBody {
  name: string
  studentId: string
  phoneNumber: string
  email: string
  department: string
  nickname: string
  password: string
}
interface RegisterRequestParams {}
interface RegisterResponseBody {
  id: number
  name: string
  studentId: string
  phoneNumber: string
  email: string
  department: string
  nickname: string
}

interface LoginRequestBody {
  email: string
  password: string
}
interface LoginRequestParams {}
interface LoginResponseBody {
  accessToken: string
  expiresIn: string
  refreshToken: string
  refreshTokenExpiresIn: string
  user: {
    id: number
    name: string
    studentId: string
    phoneNumber: string
    email: string
    department: string
    nickname: string
  }
}

interface LogoutRequestBody {}
interface LogoutRequestParams {}
interface LogoutResponseBody {}

export const AuthHandlers = [
  rest.post<LoginRequestBody, LoginRequestParams, LoginResponseBody>('/auth/login', async (req, res, ctx) => {
    const { email, password } = await req.json()
    if (email === null || password === null) {
      return res(ctx.status(400), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    if (email === 'notexist@example.com') {
      return res(
        ctx.status(401, 'user does not exist'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }
    if (password === 'notmatch') {
      return res(
        ctx.status(400, 'password does not match'),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
      )
    }

    const user = {
      id: 1,
      name: '김승현',
      studentId: '2019320048',
      phoneNumber: '010-8415-9533',
      email: 'shkim9533@korea.ac.kr',
      department: '정보대학 컴퓨터학과',
      nickname: '별 헤는 밤',
      clubs: [
        {
          id: 1,
          name: 'KWEB',
          isAdmin: true
        },
        {
          id: 2,
          name: 'KUICS',
          isAdmin: false
        },
        {
          id: 3,
          name: '그루터기',
          isAdmin: false
        },
        {
          id: 4,
          name: 'AIKU',
          isAdmin: true
        }
      ]
    }
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({
        accessToken: 'ACCESS_TOKEN',
        expiresIn: '86400',
        refreshToken: 'REFRESH_TOKEN',
        refreshTokenExpiresIn: '604800',
        user
      })
    )
  }),
  rest.post<RegisterRequestBody, RegisterRequestParams, RegisterResponseBody>(
    '/auth/register',
    async (req, res, ctx) => {
      const body = await req.json()
      if (Object.keys(body).filter(key => body[key] === null).length > 0) {
        return res(ctx.status(400), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
      }

      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      const { email, studentId, password, ...userData } = body
      if (email === 'duplicate@example.com') {
        return res(
          ctx.status(400, `email : '${email}' already exists`),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
      if (studentId === '0000000000') {
        return res(
          ctx.status(400, `'${studentId}' already exists`),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }

      return res(
        ctx.status(200),
        ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
        ctx.json({ id: 2, email, studentId, ...userData })
      )
    }
  ),
  rest.post<LogoutRequestBody, LogoutRequestParams, LogoutResponseBody>('/auth/logout', (req, res, ctx) => {
    const accessToken = req.headers.get('Authorization')
    if (accessToken !== 'Bearer ACCESS_TOKEN') {
      return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    return res(ctx.status(200), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
  })
]
