import { alpha, Box, lighten, styled, useMediaQuery, useTheme } from '@mui/material'
import { Outlet, useLocation } from 'react-router-dom'
import Footer from 'src/components/Footer'
import Header from 'src/components/Header'
import Sidebar from 'src/components/Sidebar'

const ThemeWrapper = styled(Box)(
  () => `
      position: relative;
      z-index: 5;
      display: block;
      flex: 1;
      min-width: 0;
  `
)

const SidebarLayout = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('lg'))
  const location = useLocation()

  return (
    <Box
      width="100%"
      minHeight="100%"
      sx={{
        display: 'flex',
        flexDirection: 'row',
        '.MuiPageTitle-wrapper': {
          background: theme.palette.mode === 'dark' ? theme.colors.alpha.trueWhite[5] : theme.colors.alpha.white[50],
          marginBottom: `${theme.spacing(4)}`,
          boxShadow:
            theme.palette.mode === 'dark'
              ? `0 1px 0 ${alpha(
                  lighten(theme.colors.primary.main, 0.7),
                  0.15
                )}, 0px 2px 4px -3px rgba(0, 0, 0, 0.2), 0px 5px 12px -4px rgba(0, 0, 0, .1)`
              : `0px 2px 4px -3px ${alpha(theme.colors.alpha.black[100], 0.1)}, 0px 5px 12px -4px ${alpha(
                  theme.colors.alpha.black[100],
                  0.05
                )}`
        }
      }}
    >
      <Sidebar />
      <Box
        minWidth="0"
        sx={{
          flex: 1,
          marginLeft: matches ? 0 : `calc(${theme.spacing(6)} + 1px)`
        }}
      >
        <Header />
        <ThemeWrapper>
          <Outlet />
        </ThemeWrapper>
        {(matches || !location?.pathname.startsWith('/auth')) && <Footer />}
      </Box>
    </Box>
  )
}

export default SidebarLayout
