import { useRef, useState } from 'react'

import { NavLink, useNavigate } from 'react-router-dom'

import {
  Avatar,
  Box,
  Button,
  Divider,
  Hidden,
  lighten,
  List,
  ListItem,
  ListItemText,
  Popover,
  Typography
} from '@mui/material'

import { styled } from '@mui/material/styles'
import ExpandMoreTwoToneIcon from '@mui/icons-material/ExpandMoreTwoTone'
import AccountBoxTwoToneIcon from '@mui/icons-material/AccountBoxTwoTone'
import LockOpenTwoToneIcon from '@mui/icons-material/LockOpenTwoTone'
import { useAuthHeader, useAuthUser, useIsAuthenticated, useSignOut } from 'react-auth-kit'
import axios from 'axios'
import { useSnackbar } from 'notistack'

const UserBoxButton = styled(Button)(
  ({ theme }) => `
        padding-left: ${theme.spacing(1)};
        padding-right: ${theme.spacing(1)};
`
)

const MenuUserBox = styled(Box)(
  ({ theme }) => `
        background: ${theme.colors.alpha.black[5]};
        padding: ${theme.spacing(2)};
`
)

const UserBoxText = styled(Box)(
  ({ theme }) => `
        text-align: left;
        padding-left: ${theme.spacing(1)};
`
)

const UserBoxLabel = styled(Typography)(
  ({ theme }) => `
        font-weight: ${theme.typography.fontWeightBold};
        color: ${theme.palette.secondary.main};
        display: block;
`
)

const UserBoxDescription = styled(Typography)(
  ({ theme }) => `
        color: ${lighten(theme.palette.secondary.main, 0.5)}
`
)

interface LogoutAPIResponse {}

function Userbox() {
  const navigate = useNavigate()
  const ref = useRef<any>(null)
  const isAuthenticated = useIsAuthenticated()
  const signOut = useSignOut()
  const [isOpen, setOpen] = useState<boolean>(false)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const authUser = useAuthUser()
  const user = authUser()

  const handleOpen = (): void => {
    setOpen(true)
  }

  const handleClose = (): void => {
    setOpen(false)
  }

  const handleSignout = () => {
    if (!isAuthenticated()) enqueueSnackbar('로그인 상태가 아닙니다. 페이지를 새로 고침해주세요.', { variant: 'error' })
    axios
      .post<LogoutAPIResponse>(
        `${process.env.REACT_APP_APIHOST}/auth/logout`,
        {},
        {
          headers: { Authorization: authHeader() }
        }
      )
      .then(() => {
        if (signOut()) {
          enqueueSnackbar('이용해주셔서 감사드려요. 다음에 또 만나요 :)', { variant: 'success' })
          navigate('/', { replace: true })
        } else enqueueSnackbar('로그아웃에 실패했어요. 다시 시도해주세요 T.T', { variant: 'error' })
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  return (
    <>
      <UserBoxButton color="secondary" ref={ref} onClick={handleOpen}>
        <Avatar variant="rounded" alt={user?.name} src={null} />
        <Hidden mdDown>
          <UserBoxText>
            <UserBoxLabel variant="body1">{user?.name}</UserBoxLabel>
            <UserBoxDescription variant="body2">{user?.nickname}</UserBoxDescription>
          </UserBoxText>
        </Hidden>
        <Hidden smDown>
          <ExpandMoreTwoToneIcon sx={{ ml: 1 }} />
        </Hidden>
      </UserBoxButton>
      <Popover
        anchorEl={ref.current}
        onClose={handleClose}
        open={isOpen}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
      >
        <MenuUserBox sx={{ minWidth: 210 }} display="flex">
          <Avatar variant="rounded" alt={user?.name} src={user?.avatar} />
          <UserBoxText>
            <UserBoxLabel variant="body1">{user?.name}</UserBoxLabel>
            <UserBoxDescription variant="body2">{user?.nickname}</UserBoxDescription>
          </UserBoxText>
        </MenuUserBox>
        <Divider sx={{ mb: 0 }} />
        <List sx={{ p: 1 }} component="nav">
          <ListItem to="/mypage" component={NavLink}>
            <AccountBoxTwoToneIcon fontSize="small" sx={{ marignRight: 1, mr: 1 }} />
            <ListItemText primary="마이페이지" />
          </ListItem>
        </List>
        <Divider />
        <Box sx={{ m: 1 }}>
          <Button color="primary" fullWidth onClick={handleSignout} size="small">
            <LockOpenTwoToneIcon sx={{ mr: 1 }} />
            로그아웃
          </Button>
        </Box>
      </Popover>
    </>
  )
}

export default Userbox
