export function stringToColor(string: string, alpha: number = 1.0, shade: number = 0.0) {
  let hash = 0
  let i = 0

  /* eslint-disable no-bitwise */
  for (i = 0; i < string.length; i += 1) {
    hash = string.charCodeAt(i) + ((hash << 5) - hash)
  }

  // let color = '#';
  let red = hash & 0xff
  let green = (hash >> 8) & 0xff
  let blue = (hash >> 16) & 0xff

  red = Math.ceil(red * (1 - shade))
  green = Math.ceil(green * (1 - shade))
  blue = Math.ceil(blue * (1 - shade))
  const color = `rgba(${red}, ${green}, ${blue}, ${alpha})`
  
  /* eslint-enable no-bitwise */

  return color;
}