import { rest } from 'msw'

interface UserRequestBody {}
interface UserRequestParams {}
interface UserResponseBody {
  id: number
  email: string
  studentId: string
  name: string
  nickname: string
  department: string
  phoneNumber: string
}

interface UpdateUserRequestBody {
  nickname: string
  department: string
  phoneNumber: string
}
interface UpdateUserRequestParams {}
interface UpdateUserResponseBody {
  nickname: string
  department: string
  phoneNumber: string
}

interface UpdatePasswordRequestBody {
  currentPassword: string
  newPassword: string
}
interface UpdatePasswordRequestParams {}
interface UpdatePasswordResponseBody {}

export const UserHandlers = [
  // User API
  rest.get<UserRequestBody, UserRequestParams, UserResponseBody>('/user', (req, res, ctx) => {
    const accessToken = req.headers.get('Authorization')
    if (accessToken !== 'Bearer ACCESS_TOKEN') {
      return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({
        id: 1,
        email: 'gildonghong@korea.ac.kr',
        studentId: '2020320000',
        name: '홍길동',
        nickname: '축지법 달인',
        department: '정보대학 컴퓨터학과',
        phoneNumber: '010-1234-5678'
      })
    )
  }),
  rest.put<UpdateUserRequestBody, UpdateUserRequestParams, UpdateUserResponseBody>('/user', async (req, res, ctx) => {
    const accessToken = req.headers.get('Authorization')
    if (accessToken !== 'Bearer ACCESS_TOKEN') {
      return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    const { nickname, department, phoneNumber } = await req.json()
    if (nickname === null || department === null || phoneNumber === null) {
      return res(ctx.status(400), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
    return res(
      ctx.status(200),
      ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'),
      ctx.json({ nickname, department, phoneNumber })
    )
  }),
  rest.put<UpdatePasswordRequestBody, UpdatePasswordRequestParams, UpdatePasswordResponseBody>(
    '/user/password',
    async (req, res, ctx) => {
      const accessToken = req.headers.get('Authorization')
      if (accessToken !== 'Bearer ACCESS_TOKEN') {
        return res(ctx.status(401, 'invalid user!'), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
      }
      const { currentPassword, newPassword } = await req.json()
      if (currentPassword === null || newPassword === null) {
        return res(ctx.status(400), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
      }
      if (currentPassword === 'notmatch') {
        return res(
          ctx.status(401, 'password does not match!'),
          ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000')
        )
      }
      return res(ctx.status(200), ctx.set('Access-Control-Allow-Origin', 'http://localhost:3000'))
    }
  )
]
