import { alpha, Box, Button, lighten, styled, useMediaQuery, useTheme } from '@mui/material'
import { useIsAuthenticated } from 'react-auth-kit'
import Logo from 'src/components/Logo'
import Userbox from './Userbox'

const HeaderWrapper = styled(Box)(
  ({ theme }) => `
      height: ${theme.header.height};
      background-color: ${alpha(theme.header.background, 0.95)};
      color: ${theme.header.textColor};
      position: sticky;
      top: 0;
      right: 0;
      z-index: 6;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      backdrop-filter: blur(3px);
  `
)

const HeaderItemWrapper = styled(Box)(
  () => `
      flex-grow: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
  `
)

function Header() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('lg'))
  const isAuthenticated = useIsAuthenticated()

  return (
    <HeaderWrapper
      sx={{
        boxShadow:
          theme.palette.mode === 'dark'
            ? `0 1px 0 ${alpha(
                lighten(theme.colors.primary.main, 0.7),
                0.15
              )}, 0px 2px 8px -3px rgba(0, 0, 0, 0.2), 0px 5px 22px 4px rgba(0, 0, 0, .1)`
            : `0px 2px 8px -3px ${alpha(theme.colors.alpha.black[100], 0.2)}, 0px 5px 22px -4px ${alpha(
                theme.colors.alpha.black[100],
                0.1
              )}`,
        padding: matches ? theme.spacing(0, 3) : theme.spacing(0, 2)
      }}
    >
      <HeaderItemWrapper sx={{ justifyContent: 'flex-start' }}>
        <Logo />
      </HeaderItemWrapper>
      <HeaderItemWrapper sx={{ justifyContent: 'flex-end' }}>
        {!isAuthenticated() && <Button href="/auth/login">로그인</Button>}
        {isAuthenticated() && <Userbox />}
      </HeaderItemWrapper>
    </HeaderWrapper>
  )
}

export default Header
